footer {
  background: #000;
  color: #fff;
  padding: 20px 0;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

footer ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

footer ul li a {
  color: #fff;
  text-decoration: none;
}
