#hero {
  background-color: #000;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  text-align: center;
}

.hero-content {
  max-width: 600px;
}

.hero-content h1 {
  font-size: 48px;
}

.hero-content p {
  font-size: 24px;
  margin: 20px 0;
}

.hero-content .btn {
  padding: 10px 20px;
  background: #ff0000;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
}
